
.commonDiv {
    background-color: rgb(198,198,198);
    width: max-content;
    position: relative;
    left: 1rem;
    top: 1rem;
    padding: 0.2rem;
    border: 1px solid rgb(128,128,128);
}

.header {
    height: 4rem;
    padding: 0.3rem;
    margin-bottom: 0.2rem;
    border: 5px solid rgb(128,128,128);
    font-size: 3rem;
    font-weight: bold;
    position: relative;
}

.flagsCounter {
    position: absolute;
    left: 2rem;
    top: 0.5rem;
    width: max-content;
}


.gameStatus {
    width: 4rem;
    height: 4rem;
    position: absolute;
    top: 0.3rem;
    cursor: pointer;
}
.gameStatus:active {
    transform: scale(0.95);


}


.mainFiled {
    --cellsHeightWidth: 2.5rem; /*размер всех ячеек*/
    --fontSize: 2rem;


    background-color: rgb(128,128,128);
    width: max-content;
    padding: 0.3rem;


}
.row {
    position: relative;

}

.cell {
    display: inline-block;
    width: var(--cellsHeightWidth);
    height: var(--cellsHeightWidth);
    background-color: rgb(198,198,198);
    margin: 0.05rem;
    border-radius: 0.1rem;
    font-size: var(--fontSize);
    font-family: Verdana, serif;
    font-weight: bold;
    text-align: center;
    vertical-align: top;
    cursor: pointer;
}

.cell:hover {
    /*background-color: rgb(170,170,170);*/

}


.BOOM {
    position: relative;
    width: var(--cellsHeightWidth);
    height: var(--cellsHeightWidth);
    border-radius: 0.1rem;
}

.isBombFlag {
    width: var(--cellsHeightWidth);
    height: var(--cellsHeightWidth);

}
.EmptyCells {
    width: var(--cellsHeightWidth);
    height: var(--cellsHeightWidth);

}

.blue {
    color: blue;
}
.green {
    color: green;
}
.red {
    color: red;
}
.navyBlue {
    color: rgb(0,0,128)
}
.maroon {
    color: maroon;
}

